.detail{
    .detail-info {
        padding: 30px 273px 40px 273px;
        .info-member{
            width: unset !important;
        }
    }
}
.detailcontainer{
    margin-top: 60px;
    padding-left: 273px;
    padding-top: 20px;
   
}
.info-price{
    color: #D95469;
    font-weight: 600;
    font-size: 1.8rem !important;
}
.detail-info{
    background: #f7a4b0;
    padding: 30px 0 40px 273px;
    .info-member{
        padding: 0 !important;
        width: 800px;
        .info-item{
            width: 200px;
            p{
                text-align: left !important;
            }
        }
    }
}
.detailproduct{
        p{
            margin: 50px 0;
        }
      
      
}
.more-product{
    font-size: 25px !important;
    font-weight: 600;
    color: #333;
    text-align: left;
    font-family: bungee;
    margin-bottom: 5px ;
    margin-top: 50px;
}
.product-iitem{
    display: flex;
   

    
    img{
        width: 400px;
        height: 400px;
        border: 2px solid #333;
        box-shadow: 10px 10px #969696;
        margin-right: 50px;
    }
    .product-info{
        margin-right: 70px;
        .product-name{
            font-size: 30px;
            font-weight: 600;
            font-family: 'Cascadia Code';
            margin-bottom: 10px;
        }
        .product-describe{
            margin: 10px 0 ;
            font-family: 'Cascadia Code';
        }
        .product-price{
            font-size: 30px;
            color: #D95469;
            font-family: 'Cascadia Code';
            font-weight: 600;
        }
        .count{
            display: flex;
            margin: 20px 0;
            .minus,.flus{
                width: 40px;
                height: 40px;
                border: 2px solid #333;
                text-align: center;
                line-height: 32px;
                cursor: pointer;
                &:hover{
                    background-color: yellow;
                    transition: all ease-in-out 0.6s;
                }
            }
            .minus-active{
                background-color: rgb(212, 204, 204) !important;
                cursor: unset;
            }
            .count-number{
                width: 60px;
                text-align: center;
                height: 40px;
                border: 2px solid #333;
                border-left: none;
                border-right: none;
                text-align: center;
                line-height: 32px;
            }
        }
    }
}